@tailwind base;
@tailwind utilities;
@tailwind components;
@layer base {
  html {
    font-size: 10px;
  }

  body {
    @apply antialiased text-base text-black leading-snug;
  }
  @media only screen and (min-width: 992px) {
    body {
      @apply text-md;
    }
  }

  a,
button {
    @apply cursor-pointer;
  }

  h1,
.h1 {
    @apply text-2xl-alt font-extralight;
  }
  @media only screen and (min-width: 992px) {
    h1,
.h1 {
      @apply text-5xl;
    }
  }

  h2,
.h2 {
    @apply text-4xl font-extralight;
  }

  h3,
.h3 {
    @apply text-3xl font-extralight;
  }

  h4 {
    @apply text-xl;
  }

  .copy-lg {
    @apply text-md;
  }
  @media only screen and (min-width: 992px) {
    .copy-lg {
      @apply text-lg;
    }
  }
}
@layer components {
  .mobile-nav__nav {
    margin-bottom: 160px;
  }
  .mobile-nav__trigger .arrow__outter {
    @apply border-orange;
  }
  .mobile-nav__trigger .arrow__inner {
    @apply bg-white;
  }
  .mobile-nav__trigger.active .arrow {
    @apply rotate-180;
  }
  .mobile-nav__trigger.active .arrow__outter {
    @apply border-white;
  }
  .mobile-nav .nav-menu a {
    @apply text-white uppercase text-lg my-e20 block;
  }
}
@layer components {
  .arrow {
    height: 31px;
    width: 31px;
  }
  @media (min-width: 992px) {
    .arrow {
      height: 59px;
      width: 59px;
    }
  }
  .arrow__outter {
    z-index: 12;
    height: 31px;
    width: 31px;
  }
  @media (min-width: 992px) {
    .arrow__outter {
      height: 59px;
      width: 59px;
    }
  }
  .arrow__inner {
    z-index: 10;
    top: 15px;
    left: -4px;
    width: 38px;
  }
  @media (min-width: 992px) {
    .arrow__inner {
      top: 27px;
      left: -9px;
      width: 79px;
    }
  }
  .arrow--sm {
    height: 23px;
    width: 23px;
  }
  @media (min-width: 992px) {
    .arrow--sm {
      height: 23px;
      width: 23px;
    }
  }
  .arrow--sm .arrow__outter {
    height: 23px;
    width: 23px;
  }
  @media (min-width: 992px) {
    .arrow--sm .arrow__outter {
      height: 23px;
      width: 23px;
    }
  }
  .arrow--sm .arrow__inner {
    top: 12px;
    left: -6px;
    width: 33px;
  }
  @media (min-width: 992px) {
    .arrow--sm .arrow__inner {
      width: 33px;
    }
  }
}
@layer components {
  .btn {
    @apply p-e16 text-white text-base border-2 border-orange leading-none bg-transparent font-semibold uppercase inline-flex justify-between min-w-e240 items-center;
  }
  .btn .arrow {
    @apply inline-block ml-e30;
  }
  .btn .arrow__outter {
    @apply border-orange;
  }
  .btn .arrow__inner {
    @apply bg-white;
  }
}
.image--cover img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.image--contain img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}
.image--pos-center img {
  object-position: center;
}
.image.image--ellipse {
  border-radius: 100%;
  overflow: hidden;
}

@layer components {
  .nav-menu {
    @apply flex;
  }

  .nav-link {
    @apply text-white uppercase text-base px-10;
  }
}
@layer components {
  .staggered-carousel .carousel__card:not(.carousel__card--active) .carousel__card-bk {
    transform-origin: center;
    transition: all 1s;
  }
  .staggered-carousel .carousel__card:not(.carousel__card--active):hover .carousel__card-bk {
    transform: scale(1.05);
  }
  .staggered-carousel .carousel__card {
    @apply flex mx-2 items-end flex-1 w-e240 relative h-e660 overflow-visible;
  }
  .staggered-carousel .carousel__card--active {
    @apply w-e740;
  }
  .staggered-carousel .carousel__card--active .subcards {
    @apply opacity-100;
  }
  .staggered-carousel .carousel__card-content {
    @apply relative bottom-0 px-6 overflow-hidden w-full pointer-events-none transition-all duration-500;
  }
  .staggered-carousel .subcards {
    @apply absolute bottom-0 right-0 z-150 w-e490 h-e300 bg-purple py-10 px-8 opacity-0 transition-all duration-500;
  }
  .staggered-carousel .subcards__card {
    @apply flex flex-col;
  }
  .staggered-carousel .subcards__card span {
    @apply text-white;
  }
  .staggered-carousel .subcards .subcard__progress {
    @apply w-full h-e2 bg-purple-lt;
  }
  .staggered-carousel .flickity-button {
    top: 0;
    padding: 0;
  }
}
@layer components {
  .gsap-carousel .carousel__card .carousel__card-bk {
    transform-origin: center;
    transition: all 1s;
  }
  .gsap-carousel .carousel__card:not(.carousel__card--active):hover .carousel__card-bk {
    transform: scale(1.05);
  }
  .gsap-carousel .carousel__card--a:not(.carousel__card--active):hover .carousel__card-content {
    @apply lg:h-e750;
  }
  .gsap-carousel .carousel__card--b:not(.carousel__card--active):hover .carousel__card-content {
    @apply lg:h-e710;
  }
  .gsap-carousel .carousel__card--c:not(.carousel__card--active):hover .carousel__card-content {
    @apply lg:h-e670;
  }
  .gsap-carousel .carousel__card {
    @apply flex mx-2 items-end w-e160 lg:w-e240 relative h-e460 lg:h-e640 overflow-visible;
    flex-shrink: 0;
    flex-grow: 1;
  }
  .gsap-carousel .carousel__card--active {
    @apply w-full lg:w-e740;
  }
  .gsap-carousel .carousel__card-content {
    @apply relative bottom-0 overflow-hidden w-full pointer-events-none;
    transition: all 1s;
  }
  .gsap-carousel .carousel__subcard {
    @apply absolute bottom-0 right-0 z-150 w-full lg:w-e490 h-e300 bg-purple opacity-0;
  }
  .gsap-carousel .carousel__subcard .subcard__facts .subcard__fact {
    @apply opacity-0;
  }
  .gsap-carousel .carousel__subcard .subcard__facts .subcard__fact:first-of-type {
    @apply opacity-100;
  }
  .gsap-carousel .carousel__subcard .subcard__inner {
    @apply flex flex-col h-full w-full py-10 px-8;
  }
  .gsap-carousel .carousel__subcard .subcard__inner span {
    @apply text-white;
  }
  .gsap-carousel .carousel__subcard .subcard__progress-track {
    @apply w-full h-e2 bg-purple-lt mb-6 relative;
  }
  .gsap-carousel .carousel__subcard .subcard__progress {
    @apply bg-white w-0;
  }
}