@layer components {
  .btn {
    @apply p-e16 text-white text-base border-2 border-orange leading-none bg-transparent font-semibold uppercase inline-flex justify-between min-w-e240 items-center;

    .arrow {
      @apply inline-block ml-e30;
    }

    .arrow__outter {
      @apply border-orange;
    }

    .arrow__inner {
      @apply bg-white;
    }
  }
}