.image {
  &--cover {
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  &--contain {
    img {
      object-fit: contain;
      width: 100%;
      height: 100%;
    }
  }

  &--pos-center {
    img {
      object-position: center;
    }
  }

  &.image--ellipse {
    border-radius: 100%;
    overflow: hidden;
  }
}