@layer components {
  .arrow {
    height: 31px;
    width: 31px;

    @media (min-width: 992px) {
      height: 59px;
      width: 59px;
    }

    &__outter {
      z-index: 12;
      height: 31px;
      width: 31px;

      @media (min-width: 992px) {
        height: 59px;
        width: 59px;
      }
    };
    
    &__inner {
      z-index: 10;
      top: 15px;
      left: -4px;
      width: 38px;

      @media (min-width: 992px) {
        top: 27px;
        left: -9px;
        width: 79px;
      }
    }

    &--sm {
      height: 23px;
      width: 23px;

      @media (min-width: 992px) {
        height: 23px;
        width: 23px;
      }

      .arrow__outter {
        height: 23px;
        width: 23px;

        @media (min-width: 992px) {
          height: 23px;
          width: 23px;
        }
      }
      
      .arrow__inner {
        top: 12px;
        left: -6px;
        width: 33px;

        @media (min-width: 992px) {
          width: 33px;
        }
      }
    }
  }
}