@layer components {
  .gsap-carousel {
    .carousel {
      &__card .carousel__card-bk {
        transform-origin: center;
        transition: all 1s;
      }

      &__card:not(.carousel__card--active):hover .carousel__card-bk {
        transform: scale(1.05);
      }

      &__card--a:not(.carousel__card--active):hover .carousel__card-content {
        @apply lg:h-e750;
      }

      &__card--b:not(.carousel__card--active):hover .carousel__card-content {
        @apply lg:h-e710
      }

      &__card--c:not(.carousel__card--active):hover .carousel__card-content {
        @apply lg:h-e670
      }
 
      &__card {
        @apply flex mx-2 items-end w-e160 lg:w-e240 relative h-e460 lg:h-e640 overflow-visible;
        flex-shrink: 0;
        flex-grow: 1;
      }

      &__card--active {
        @apply w-full lg:w-e740;
      }

      &__card-content {
        @apply relative bottom-0 overflow-hidden w-full pointer-events-none;
        transition: all 1s;
      }

      &__subcard {
        @apply absolute bottom-0 right-0 z-150 w-full lg:w-e490 h-e300 bg-purple opacity-0;

        .subcard__facts .subcard__fact {
          @apply opacity-0;

          &:first-of-type {
            @apply opacity-100;
          }
        }

        .subcard__inner {
          @apply flex flex-col h-full w-full py-10 px-8;

          span {
            @apply text-white;
          }
        }
        
        .subcard__progress-track {
          @apply w-full h-e2 bg-purple-lt mb-6 relative;
        }

        .subcard__progress {
          @apply bg-white w-0;
        }
      }
    }
  }
}