@layer components {
  .mobile-nav {
    &__nav {
      margin-bottom: 160px;
    }

    &__trigger {
      .arrow__outter {
        @apply border-orange;
      }

      .arrow__inner {
        @apply bg-white;
      }
    }
    &__trigger.active {
      .arrow {
        @apply rotate-180;
      }

      .arrow__outter {
        @apply border-white;
      }
    }

    .nav-menu a {
      @apply text-white uppercase text-lg my-e20 block;
    }
  }
}