@layer base {

  html {
    font-size: 10px;
  }

  body {
    @apply antialiased text-base text-black leading-snug;

    @include bp-992 {
      @apply text-md;
    }
  }

  a,
  button {
    @apply cursor-pointer;
  }

  h1,
  .h1 {
    @apply text-2xl-alt font-extralight;

    @include bp-992 {
      @apply text-5xl;
    }
  }

  h2,
  .h2 {
    @apply text-4xl font-extralight;
  }

  h3,
  .h3 {
    @apply text-3xl font-extralight;
  }

  h4 {
    @apply text-xl;
  }

  .copy-lg {
    @apply text-md;

    @include bp-992 {
      @apply text-lg;
    }
  }
}